body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}
:root {
  --gray: rgb(149, 151, 153);
  --peach: rgb(248, 160, 120);
}

.placeholder {
  height: 120px;
}

.header {
  display: flex;
  height: 100px;
  width: 100%;
  padding-bottom: 20px;
  position: fixed;
  top: 0;
  z-index: 2;
  background: white;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.header.active {
  background: rgb(248, 160, 120);
  background: var(--peach);
}

.header.active a,
.header.active .subtitle,
.header.active .social {
  color: white;
}

.header-item {
  align-items: center;
  display: flex;
  flex: 1 1;
}

.navigation {
  margin-left: 40px;
}

.navigation-item {
  color: rgb(149, 151, 153);
  color: var(--gray);
  margin: 0 10px;
}

.navigation-item:hover {
  color: rgb(248, 160, 120);
  color: var(--peach);
}

.title-container {
  display: inline-block;
  margin-top: 20px;
}

.title {
  color: rgb(33, 121, 169);
  display: flex;
  font-size: 50px;
  justify-content: center;
}

.subtitle {
  color: rgb(149, 151, 153);
  color: var(--gray);
  display: flex;
  justify-content: center;
}

.social {
  justify-content: flex-end;
  margin-right: 40px;
}

.social-item {
  font-size: 20px;
  margin: 0 10px;
}

.social-item:hover {
  color: rgb(248, 160, 120);
  color: var(--peach);
}
.profile {
  max-width: 800px;
  padding: 0 5px;
  margin: 0 auto;
}

.gallery {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(6, 1fr);

  /* To by dynamically set*/
  grid-auto-rows: 90px;
}

.card {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  grid-column-end: span 2;
  grid-row-end: span 2;
}

.card img {
  display: flex;
  height: 100%;
  width: 100%;
  object-fit: cover;
}


.thumbnail {
  width: 150px;
  height: 150px;
  /*background: red;*/
  display:flex;
  align-items:center;
  justify-content:center;
}

.thumbnail img {
  max-width: 100%;
  max-height: 100%;
}

.thumbnail-selected {
  border: 3px solid;
}
.container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 1;
  background: white;
  padding-top: 120px;
  display: flex;
}

.side {
  flex: 1 1;
}

.cancel-button {
  color: black;
  font-size: 30px;
}

.cancel-button:hover {
  color: var(--peach);
}

.carousel-container {
  width: 100%;
}

.carousel {
  margin: 0 auto;
  max-width: 830px;
}

.carousel-item {
  width: calc(100% - 40px);
  height: 400px;
  overflow: hidden;
  display:flex;
  align-items:center;
  justify-content:center;
  flex-flow: column;
}

.carousel-item-image {
  display: flex;
  flex: 1 1;
  align-items:center;
  justify-content:center;
  width: 100%;
  height: 100%;
}
.carousel-item-image img {
  max-width: 100%;
  max-height: 100%;
}

.carousel-item-primary {
  font-size: 20px;
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 5px;
}

.carousel-item-secondary {
  font-size: 12px;
  margin-bottom: 10px;
}

.arrow {
  padding: 40px;
  font-size: 40px;
  width: 15px;
}

.thumbnails {
  margin: 20px auto 0 auto;
  max-width: 750px;
}
